* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Barlow Condensed', sans-serif;
font-family: 'Kanit', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Roboto Slab', serif;
