/* Home Section */

.homesection{
  position: relative;
}

.text_us{


  position: sticky;
  top: 90%;
  left:90%;
  height: 60px;
  width: fit-content;
  padding: 10px;
  background-color:#000;
  color: whitesmoke;
  display: flex;
  align-items: center;
  gap: 20px;
  border-radius: 20px;
  justify-content: center;
  z-index: 100;
  cursor: pointer;
}

.cancel_form{
  position: sticky;
  top: 85%;
  left:90%;
  margin-left: 90%;
  font-size: 60px;
  color: red;
  text-align: center;
  cursor: pointer;
  z-index: 100;
}

/* ///////////////////////////// */

.message-container {
  position:absolute;
  top: 4%;
  right:5%;
  height: 70vh;
  width: 25%;
  color: #000;
  background-color: #FFFFFF;
  border: 1px solid #ccc; 
  box-sizing: border-box; 
  display: flex;
  flex-direction: column;
  border-radius:10px ;
  justify-content: space-between; 
  overflow: hidden;
 
  z-index: 100;
}



.message-container .response{
  display: flex;
  align-items: center;
  gap: 6px;
  color: #FFFFFF;
  padding: 30px;
  font-weight: 700;
  font-size: 20px;
 font-style: normal;
 line-height: 20px;
  background-color: blue;
  transition: all ease-in-out 1s;
 
}
.formstacky{

  position: sticky;
  top: 15%;
  z-index: 60;
}

.form_content{

  height: 30vh;
  overflow-y: scroll;
  background-color: #FFFFFF;
  padding: 30px;
}
.your_information{

  background-color: gray;
  padding: 10px;
  border-top-left-radius: 20px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  margin-bottom: 10px;
  transition: all ease-in-out 1s;
  /* border-radius: 10px; */
}
form {
  background-color: #FFFFFF;
  margin-top: 60px;
}

.input-container {
  position: relative;
  margin-top: 30px;
}

.form_class input,
.form_class textarea {
  border: none;
  outline: none;
  border-bottom: 1px solid gray;
  padding-top: 20px;
  z-index: 3;
  width: 100%;
  box-sizing: border-box;
}

.placeholder-label {
  position: absolute;
  top: 8px;
  left: 16px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease-out;
  color: black;
}

.form_class input:focus,
.form_class textarea:focus {
  border-bottom: 3px solid blue;
}
.form_class input:focus::placeholder,
.form_class textarea:focus::placeholder{

  opacity: 0;
}

.form_class input:focus + .placeholder-label,
.form_class textarea:focus + .placeholder-label,
.form_class input:not(:placeholder-shown) + .placeholder-label,
.form_class textarea:not(:placeholder-shown) + .placeholder-label {
  transform: translateY(-20px);
  font-size: 12px;
  opacity: 1;
  color: blue;
}

.textareaa{

  font-size: 13px;
  font-weight: 100;
  opacity: 0.8;
  font-style: normal;
}

.butt{

  margin-top: 40px;
 padding: 10px 30px 10px 30px;
 border-radius: 8px;
 cursor: pointer;
 margin-left: 30%;
 font-weight: 300;
 letter-spacing: 2px;
 font-size: 16px;
 color: #FFFFFF;
 background-color: rgb(108, 108, 217);
 border: 0;
 outline: none;
 transition: all ease-in-out 1s;
}

.butt:hover{

  color: red;
  letter-spacing: 4px;
 background-color: rgb(8, 8, 81);

}

.botton_text{

  width: 100%;
  padding: 10px;
  padding-bottom: 30px;
  /* text-decoration: none; */
}
.botton_text a{

  font-size: 13px;
  padding-left: 10px;
  color: aqua;
}
.middle_text{
  color: rgba(0, 0, 0, 0.527);
  margin-right: 20px;
  margin-left: 10px;
}





