/* container  */

.single_family_home, .townhomes{
 height: 68vh;
 object-fit: fill;
 width: 55%;
 text-align: center;
 margin-left: -30px;
position: relative;
overflow: hidden;
cursor: pointer;
}


.single_family_home img, .townhomes img{
 width: 98%;
 height: 100%;
 scale: 1.02;
 z-index: 1;
 transition: all ease-in-out 1s;

}
.single_family_home:hover img, .townhomes:hover img{
  scale: 1;
}

.single_family_home:after{

  position: absolute;
  content: "";
  top: 11.5%;
  left: 0;
  width: 100%;
  height: 105%;
  opacity: 0.5;
  z-index: 0;
  background-color: #000;
  transition: all ease-in-out 1s;
}

.townhomes::after{

  position: absolute;
  content: "";
  top: 11.5%;
  left: 4.3%;
  width: 100%;
  height: 105%;
  opacity: 0.5;
  z-index: 0;
  background-color: #000;
  transition: all ease-in-out 1s;
}
.single_family_home:hover:after, .townhomes:hover:after{
  opacity: 0;
}
.single_family_home h2, .townhomes h2{
  position:absolute;
  top: 40%;
  left: 30%;
  font-weight: 500;
  font-size: 35px;
  text-align: center;
  margin-top: 5%;
  line-height: 20px;
  letter-spacing: 0;
  color: #FFFFFF;
  z-index: 3;
}
.single_family_home h4, .townhomes h4{
  position:absolute;
  top: 50%;
  left: 30%;
  font-weight: 100;
  font-size: 25px;
  text-align: center;
  margin-top: 5%;
  margin-left: 30px;
  line-height: 20px;
  letter-spacing: 0;
  opacity: 0.8;
  color: #FFFFFF;
  z-index: 3;
}

.arrow{
  position: absolute;
