/* photogallery_container */


.photogallery_container{

  height: 120vh;
  width: 100%;
}
.photogallery_heading{

  margin-top: 90%;
  align-items: center;
  font-size: 50px;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 24px;
  color: #474747;
  margin-left: 50%;
  position: relative;
}
.photogallery_heading::before{
 position:absolute;
 content: " ";
 left: 10%;
 bottom: -220%;
 height: 4px;
 width: 10%;
 background-color: #ee2e24;


}

/* photo_gallery */

.photo_gallery{

  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-left: 60px;
  cursor: pointer;
  overflow: hidden;
 

 
}
.photogallery_items{
  height: 400px;
  width: 500px;
  position: relative;
  /* overflow: hidden; */
 
  
}
.photogallery_items::before{
  position: absolute;
  content: "";
  top: 15%;
  left: -10%;
  width: 100%;
  height: 100%;
  background-color: #ee2e24;
  z-index: 11;
  opacity: 0;
  scale: 0.8;
  transition: all ease-out 0.5s;
}

.photogallery_items:hover::before{
  opacity: 0.8;
  scale: 1;
}
.photo_gallery img{
  margin-left: -10%;
  width: 100%;
  height: 100%;
  /* scale: 1.05; */
  object-fit: cover;
  transition: all ease-in-out 1s;
}
.photogallery_items:hover img{
  scale: 1;
}


.photo_gallery h2{

  position: absolute;
  top: 50%;
  left: 30%;
  z-index: 20;
  font-family: Oswald, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 48px;
  color: #FFFFFF;
}

